#products {
    .hero-banner {
        background-image: url('../../assets/products/hero-banner.png');
        height: 500px;
        background-size: cover;
        background-position: center;

        .blur-back-dark {
            height: 100%;
            background-color: #333333b3;

            .container {
                width: 100%;
                height: 100%;
                display: flex;
                justify-content: center;
                align-items: center;
                color: #FFFFFF;
                font-size: 40px;
            }
        }
    }

    .product-section {
        margin-bottom: 50px;

        .section-heading {
            margin-top: 50px;
            padding: 20px 0;
            text-align: left;
            text-underline-offset: 8px;
        }

        .product-list {
            display: grid;
            grid-template-columns: repeat(auto-fit, 250px);
            gap: 50px;
            padding: 20px;
            width: 100%;

            .product-card {
                border-radius: 10px;
                box-shadow: 0px 0px 20px 0px #000000b3;
                text-align: center;
                padding: 10px;

                img {
                    width: 100%;
                    object-fit: cover;
                }

                h4 {
                    font-size: 20px;
                    font-weight: 600;
                    margin: 10px;
                }

                p {
                    margin-top: 5px;
                }
            }
        }


        @media (max-width: 768px) {
            .product-list {
                gap: 20px;
                padding: 0 20px;
                width: 100%;

                .product-card {
                    width: 100%;
                    margin-bottom: 25px;

                    img {
                        object-fit: cover;
                    }
                }
            }
        }
    }
}